@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Circular', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  }

  body {
    @apply bg-white text-airbnb-dark;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-bold text-airbnb-dark;
  }

  a {
    @apply text-airbnb-pink hover:text-airbnb-pink/80 transition-colors duration-300;
  }
}

@layer components {
  .btn {
    @apply px-6 py-3 rounded-full font-medium transition-all duration-300;
  }

  .btn-primary {
    @apply bg-airbnb-pink text-white hover:bg-airbnb-pink/90;
  }

  .btn-secondary {
    @apply bg-white text-airbnb-dark border border-gray-200 hover:border-gray-300;
  }

  .card {
    @apply bg-white rounded-2xl border border-gray-100 hover:border-gray-200 transition-all duration-300 overflow-hidden hover:shadow-lg;
  }

  .input {
    @apply w-full px-4 py-3 rounded-lg border border-gray-200 focus:border-airbnb-pink focus:ring-1 focus:ring-airbnb-pink outline-none transition-all duration-300;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 rounded-full hover:bg-gray-400 transition-colors duration-300;
}
